@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap');

* {
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    transition: all .5s;
}

:root {
    --light: white;
    --mid: #00c17c;
    --dark: #212626;
    --dark2: #6e6d6e;
    --dark3: #8d8a94;
}

body {
    background-color: var(--dark);
    transition: none;
}